/*----- dashboard-timeline ITEM -----*/
.dashboard-timeline-item {
  position: relative;
  padding: 0px;
  padding-left: 30px;
  padding-right: 30px;
  padding-bottom: 10px;
}
.dashboard-timeline-item:last-child {
  padding-bottom: 0;
}

/*----- dashboard-timeline INFO -----*/
.dashboard-timeline-info {
  font-size: 12px;
  font-weight: 500;
  letter-spacing: 2px;
  margin: 0 0 0.5em 0;
  white-space: nowrap;
}

/*----- dashboard-timeline MARKER -----*/
.dashboard-timeline-marker {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  width: 15px;
}
.dashboard-timeline-marker:before {
  background: #622eb4;
  border: 3px solid transparent;
  border-radius: 100%;
  content: "";
  display: block;
  height: 15px;
  position: absolute;
  top: 4px;
  left: 0;
  width: 15px;
  transition: background 0.3s ease-in-out, border 0.3s ease-in-out;
}
.dashboard-timeline-marker:after {
  content: "";
  width: 3px;
  background: #ccd5db;
  display: block;
  position: absolute;
  top: 24px;
  bottom: 0;
  left: 6px;
}
.dashboard-timeline-item:last-child .dashboard-timeline-marker:after {
  content: none;
}

.dashboard-timeline-item:not(.period):hover .dashboard-timeline-marker:before {
  background: transparent;
  border: 3px solid #622eb4;
}

/*----- dashboard-timeline CONTENT -----*/
.dashboard-timeline-content {
  padding-bottom: 20px;
}
.dashboard-timeline-content p:last-child {
  margin-bottom: 0;
}

